import { supabase } from '../utils/supabase';

// Task  
export const taskService = {
  //   
  async getAll() {
    const { data, error } = await supabase
      .from('tasks')
      .select('*')
      .order('created_at', { ascending: false });
    
    if (error) throw error;
    return data || [];
  },

  //  
  async create(task: { title: string; description: string; area: string; status?: string }) {
    const { data, error } = await supabase
      .from('tasks')
      .insert([{ ...task, status: task.status || 'pending' }])
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  //  
  async update(id: string, updates: Partial<{ title: string; description: string; area: string; status: string; google_sheet_url: string }>) {
    const { data, error } = await supabase
      .from('tasks')
      .update(updates)
      .eq('id', id)
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  //  
  async delete(id: string) {
    const { error } = await supabase
      .from('tasks')
      .delete()
      .eq('id', id);
    
    if (error) throw error;
  },

  //    
  async addVocabularyItems(taskId: string, items: Array<{ unit: string; english: string; meaning: string }>) {
    //    
    await supabase
      .from('vocabulary_items')
      .delete()
      .eq('task_id', taskId);

    //   
    if (items.length > 0) {
      //    (Supabase   500)
      const BATCH_SIZE = 500;
      const totalBatches = Math.ceil(items.length / BATCH_SIZE);
      
      console.log(` ${items.length}  ${totalBatches}   .`);
      
      //   
      for (let i = 0; i < totalBatches; i++) {
        const start = i * BATCH_SIZE;
        const end = Math.min(start + BATCH_SIZE, items.length);
        const batch = items.slice(start, end);
        
        const vocabularyData = batch.map((item, index) => ({
          task_id: taskId,
          unit: item.unit,
          english: item.english,
          meaning: item.meaning,
          order_index: start + index //   
        }));

        const { error } = await supabase
          .from('vocabulary_items')
          .insert(vocabularyData);
        
        if (error) {
          console.error(` ${i + 1}/${totalBatches}  :`, error);
          throw error;
        }
        
        console.log(` ${i + 1}/${totalBatches}   (${start + 1}-${end} )`);
      }
      
      console.log(` ${items.length}   `);
    }
  },

  //    
  async getVocabularyItems(taskId: string) {
    const allItems = [];
    let from = 0;
    const pageSize = 1000;
    let hasMore = true;
    
    console.log(` ${taskId}    ...`);
    
    while (hasMore) {
      const { data, error } = await supabase
        .from('vocabulary_items')
        .select('*')
        .eq('task_id', taskId)
        .order('order_index')
        .range(from, from + pageSize - 1);
      
      if (error) {
        console.error('   :', error);
        throw error;
      }
      
      if (data && data.length > 0) {
        allItems.push(...data);
        console.log(`${from + 1} ~ ${from + data.length}   `);
        from += pageSize;
        hasMore = data.length === pageSize;
      } else {
        hasMore = false;
      }
    }
    
    console.log(` ${taskId}  ${allItems.length}   `);
    return allItems;
  }
};

// Student  
export const studentService = {
  //   
  async getAll() {
    const { data, error } = await supabase
      .from('students')
      .select('*')
      .order('created_at', { ascending: false });
    
    if (error) throw error;
    return data || [];
  },

  //  
  async create(student: { name: string; level: string }) {
    const { data, error } = await supabase
      .from('students')
      .insert([student])
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  //  
  async update(id: string, updates: Partial<{ name: string; level: string }>) {
    const { data, error } = await supabase
      .from('students')
      .update(updates)
      .eq('id', id)
      .select()
      .single();
    
    if (error) throw error;
    return data;
  },

  //  
  async delete(id: string) {
    const { error } = await supabase
      .from('students')
      .delete()
      .eq('id', id);
    
    if (error) throw error;
  },

  //    
  async getById(id: string) {
    const { data, error } = await supabase
      .from('students')
      .select('*')
      .eq('id', id)
      .single();
    
    if (error) throw error;
    return data;
  }
};

// Task Assignment ( )  
export const assignmentService = {
  //   
  async assignStudentToTask(taskId: string, studentId: string) {
    const { error } = await supabase
      .from('task_assignments')
      .insert([{ task_id: taskId, student_id: studentId }]);
    
    if (error && error.code !== '23505') throw error; //   
  },

  //    
  async unassignStudentFromTask(taskId: string, studentId: string) {
    const { error } = await supabase
      .from('task_assignments')
      .delete()
      .eq('task_id', taskId)
      .eq('student_id', studentId);
    
    if (error) throw error;
  },

  //    
  async getStudentsByTask(taskId: string) {
    const { data, error } = await supabase
      .from('task_assignments')
      .select('student_id, students(*)')
      .eq('task_id', taskId);
    
    if (error) throw error;
    return data?.map(item => item.students) || [];
  },

  //    
  async getTasksByStudent(studentId: string) {
    const { data, error } = await supabase
      .from('task_assignments')
      .select('task_id, tasks(*)')
      .eq('student_id', studentId);
    
    if (error) throw error;
    return data?.map(item => item.tasks) || [];
  }
}; 